import React, { Component } from 'react'
import CommandLogger from './CommandLogger'

export class App extends Component {
  render () {
    return (
      <box>
        <box ref="left" top="4%" left="2%" width="20%" border={{type: 'line'}} style={{border:{fg:'cyan'}}}>
          <CommandLogger command={{cmd:'skypager serve'}} />
        </box>
        <box ref="right" top="4%" width="" width="60%" left="33%">
          <CommandLogger cmd='skypager dev' />
        </box>
      </box>
    )
  }
}

export default App



